                <select class="custom-select mb-2" v-validate="'required'" v-model="reports.type"
                  name="違規性質" :class="{'is-invalid': errors.has('違規性質')}">
                  <option disabled selected value="">請選擇違規性質</option>
                  <option value="內容包含色情、暴力等不法言論">內容包含色情、暴力等不法言論</option>
                  <option value="侵權、抄襲問題">侵權、抄襲問題</option>
