<script>
import firebase from 'firebase';
import Login from '../components/Login.vue'

export default {
  data() {
    return {
      isLoading: false,
      showData: [],
      userData: null,
      tempDom: {
        filterItem: ['全部', '活動表演', '公益活動', '指導教學', '商業表演','其他'],
        selectCity: '',
        selectType: '全部',
        login: false,
        loginopened: false,
      },
      tempData: {
        allData: [],
        options: ['全部', '活動表演', '公益活動', '指導教學', '商業表演','其他'],
      },
      reports: {
        id: '',
        type: '',
        msg: '',
      }
    }
  },
  watch: {
    'tempDom.selectCity': {
      handler: function filterItems(val) {
        const vm = this;
        vm.filterData();
      },
    },
    'tempDom.selectType': {
      handler: function filterItems(val) {
        const vm = this;
        vm.filterData();
      },
    },
  },
  components:{
    Login
  },
  methods: {
    getData() {
      const vm = this;
      vm.isLoading = true;
      const url = `${process.env.VUE_APP_API}/getPostData`;
      vm.$http.post(url, {"url": '/show'}).then((response) => {
        const obj = response.data;
        const keys = Object.keys(obj);
        vm.tempData.allData = keys.map(key => obj[key]);
        vm.showData = vm.tempData.allData;
        vm.isLoading = false;
      })
    },
    filterData() {
      const vm = this;
      if(vm.tempDom.selectCity && vm.tempDom.selectType !== '全部') {
        let data = vm.tempData.allData.filter((item) => {
          return item.city === vm.tempDom.selectCity && vm.cityData(item.type)
        })
        vm.showData = data;
      } else if(vm.tempDom.selectCity) {
        let data = vm.tempData.allData.filter((item) => {
          return item.city === vm.tempDom.selectCity
        })
        vm.showData = data;
      } else if(vm.tempDom.selectType !== '全部') {
        let data = vm.tempData.allData.filter((item) => {
          return vm.cityData(item.type)
        })
        vm.showData = data;
      } else {
        vm.showData = vm.tempData.allData;
      }
    },
    cityData(obj) {
      const vm = this;
      const keys = Object.keys(obj).map(key => obj[key]);
      return keys.indexOf(vm.tempDom.selectType) !== -1
    },
    getUserData() {
      const vm = this;
      const url = `${process.env.VUE_APP_API}/getData`;
      firebase.auth().onAuthStateChanged(function(user) {
        if (user) {
          vm.$http.post(url, {"url": `users/${user.uid}/basic/`}).then((response) => {
            vm.userData = response.data;
          })
        } else {
          vm.userData = null;
        }
      });
    },
    report() {
      const vm = this;
      let str = `
        <h1>檢舉《我想表演》貼文通知</h1>
        <b>貼文編碼: ${vm.reports.id}</b>
        <p>舉報原因: ${vm.reports.type}</p>
        <p>理由及證據:<br> ${vm.reports.msg}</p>
        <hr>
        <h2>舉報人資訊</h2>
        <b>舉報人 email: ${vm.userData.email}</b>
        <p>舉報人暱稱: ${vm.reports.displayName}</p>
      `;
      this.$validator.validate().then((result) => {
        if (result) {
          // do stuff if not valid.
          vm.sendReportMail(str);
        } else {
          // console.log('欄位不完整')
        }
      });
    },
    sendReportMail(str) {
      const vm = this;
      const url = `${process.env.VUE_APP_API}/report`;
      vm.isLoading = true;
      this.$http.post(url, {"str": str}).then((response) => {
        vm.isLoading = false;
      })
      .catch(function(error){
        console.error("錯誤",error);
      });
    },
    resetReportError(idid) {
      this.errors.clear();
      this.reports.id = idid;
      this.reports.type = '';
      this.reports.msg = '';
      // this.errors.remove(field);
    }
  },
  created() {
    const vm = this;
    vm.$bus.$on('close:login',(val) => {
      vm.tempDom.login = val;
    })
    vm.getData();
    vm.getUserData();
  },
}
</script>